import React from 'react';

const formUrl = 'https://script.google.com/macros/s/AKfycbwxBnufHTIYLtxiDCl5x_vp_GlQsg7GKUrGyDCO57UrWpDkQwKVxPTBYAS9zt8uWBkZMQ/exec'
export default function MuluTable(){  
    
    return(
      <div className="contactform">
        <form name="mulu" method="post" action={formUrl} >   
          <div className="form-elements">
            <div className="form-group">
              <label className="form-group-label">Name</label>
              <input id="Name" name="name" className="form-group-text" type="text"/>
            </div>

            <div className="form-group">
              <label className="form-group-label">Email</label>
              <input id="Email" name="email" className="form-group-text" type="text"/>
            </div> 
            
            <div className="form-group">
              <label className="form-group-label" >Message</label>
              <textarea id="Message" name="message" rows="10" className="form-group-text" placeholder="" maxLength="1000"></textarea>
            </div>
          </div>

          <div className="form-group form-group-btn, col-12">
            <button type="submit" className="button">
              <span className="button-text">Send Message</span>
              <span className="button-icon fa fa-send"></span>
            </button>
          </div>
        </form>
      </div>
    )
}
