import React from "react";

const ModalOneBlogContent = () => {
  return (
    //  Article Starts
    <article>
      <div className="title-section text-left text-sm-center">
        <h1>
          Post <span>Snippet</span>
        </h1>
        <span className="title-bg">posts</span>
      </div>
      {/* Meta Starts */}

      <div className="meta open-sans-font">
        <span>
          <i className="fa fa-user"></i> Mulu
        </span>
        <span className="date">
          <i className="fa fa-calendar"></i> 14 November 2022
        </span>
        <span>
          <i className="fa fa-tags"></i> React, JavaScript, Cryptocurrency
        </span>
      </div>
      {/* Meta Ends */}
      {/* Article Content Starts */}

      <h1>Aplikasi JavaScript berbasis React yang memungkinkan Anda membuat pelacak waktu nyata Cryptocurrency sendiri</h1>
      <img src="img/blog/blog-post-1a.png" className="img-fluid" alt="Blog" />
      <div className="blog-excerpt open-sans-font pb-5">
        <p>
          Fitur:
            Referensi Changelly,
            API CoinCap,
            Data Langsung,
            Lebih dari 1000 Koin,
            UI bersih,
            Dukungan Multi Mata Uang,
        </p>

        <div className="quotebox">
          <div className="icon">
            <img src="img/blog/quote.svg" alt="blog quote" />
          </div>
          <p>
            You can never cross the ocean until you have the courage to lose sight of the shore.  Christopher Columbus
          </p>
          <p>
            <a href="https://radiomulu.github.io" target="_blank" rel="noreferrer">
            <i className="fa fa-github"></i> Kunjungi Repositori..
            </a>
          </p>
        </div> 
      </div>
      {/* Article Content Ends */}
    </article>
    // Article Ends
  );
};

export default ModalOneBlogContent;
