import React from "react";

const SocialShare = [
  {
    iconName: "fa fa-instagram",
    link: "https://www.instagram.com/radiomulu",
  },
  { iconName: "fa fa-twitter", link: "https://twitter.com/radiomulu" },
  {
    iconName: "fa fa-youtube",
    link: "https://youtube.com/channel/UCNtPVcORhTHT14klY39zqDw",
  },
  { 
    iconName: "fa fa-telegram", link: "https://www.telegram.org/muluveee" 
  },
  { 
    iconName: "fa fa-linkedin", link: "https://www.linkedin.com/in/muhammad-lutfi-95843a241" 
  },
];

const Social = () => {
  return (
    <ul className="social list-unstyled pt-1 mb-5">
      {SocialShare.map((val, i) => (
        <li key={i}>
          <a href={val.link} target="_blank" rel="noreferrer">
            <i className={val.iconName}></i>
          </a>
        </li>
      ))}
    </ul>
  );
};

export default Social;
