import React from "react";

const ModalTwoBlogContent = () => {
  return (
    //  Article Starts
    <article>
      <div className="title-section text-left text-sm-center">
        <h1>
          Post <span>Snippet</span>
        </h1>
        <span className="title-bg">posts</span>
      </div>
      {/* Meta Starts */}

      <div className="meta open-sans-font">
        <span>
          <i className="fa fa-user"></i> Mulu
        </span>
        <span className="date">
          <i className="fa fa-calendar"></i> 26 Maret 2022
        </span>
        <span>
          <i className="fa fa-tags"></i> Cryptocurrency, business, economy, design
        </span>
      </div>
      {/* Meta Ends */}
      {/* Article Content Starts */}

      <h1>5 Langkah Penting Sebelum Memutuskan Berinvestasi (Cryptocurrency)</h1>
      <img src="img/blog/blog-post-2.jpg" className="img-fluid" alt="Blog" />
      <div className="blog-excerpt open-sans-font pb-5">
        <p>
          Menurut data dari Triple A , lebih dari 7,2 juta penduduk Indonesia atau sekitar 2,66% populasi di negeri ini, tercatat telah menjadi pengguna cryptocurrency .

          WOW !

          Kabar baik?

          Saya sejujurnya antusias namun sekaligus was-was mengetahui data ini. Mengapa?

          Sepertinya kita semua sepakat, bahwa dunia investasi khususnya cryptocurrency ibarat pedang bermata dua.

          Satu sisi dapat memberi kita keuntungan luar biasa, sisi lain siap menelikung dan menyeret kita ke jurang kebangkrutan.
        </p>
        <div className="quotebox">
          <div className="icon">
            <img src="img/blog/quote.svg" alt="blog quote" />
          </div>
          <p>
            Going to war with blind eyes, that's not at war, that's suicide. -Mulu
          </p>
        </div>
        <p>
          <a href="https://radiomulu.medium.com/" target="_blank" rel="noreferrer">Lanjutkan membaca <i className="fa fa-external-link pr-2"></i>
            </a>
        </p> 
      </div> 
      {/* Article Content Ends */}
    </article>
    // Article Ends
  );
};

export default ModalTwoBlogContent;
